/**
 * #.# Styles
 *
 * CSS for FrontEnd + BackEnd.
 */

 .k2-pb-parent-container{
    display: flex;
    flex-wrap: wrap;
    margin: 2em 0.5em;
  }
  .k2-pb-sub-parent-container {
  
  }
  .k2-pb-outside-container {
    display: inline-flex;
    width: 100%;
    box-shadow: inset 0 0px 1px rgba(0, 0, 0, 0.2),
    inset 0 -2px 1px rgba(0, 0, 0, 0.2);
  }
  .k2-pb-inside-container {
    display: block;
    background-color: green;
    position: relative;
    overflow: hidden;
    background-size: 100%;
    box-shadow: inset 0 0px 1px rgba(0, 0, 0, 0.2),
    inset 0 -2px 1px rgba(0, 0, 0, 0.2);
  }
  
  .k2-pb-inside-container span{
  
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    content: "";
    background-image: linear-gradient(45deg, white 25%, transparent 25%, transparent 50%, white 50%, white 75%, transparent 75%, transparent);
    background-size: 30px 30px;
    animation: k2-pb-candy-effect 0.5s infinite linear;
  }
  
  
  @keyframes k2-pb-candy-effect {
    from {
      background-position: 0 100%;
    }
    to {
      background-position: 30px 100%;
    }
  }