.k2_ld_course_grid_parent_container_save{
  display: flex;
  width: 95%;
  margin-left: auto !important;
  margin-right: auto !important;
  flex-wrap: wrap;
  gap: 5%;
  row-gap: 50px;

  .k2_ld_course_grid_item{
    width: 30%;
    .k2_ld_course_image{
      position: relative ;
      img{
        border-top-left-radius: 10px;
        border-top-right-radius: 10px;
      }
      .k2_ld_course_image_tag{
        position: absolute;
        top: 20px;
        left: 20px;
        right: auto;
        width: 110px;
        background-color: #2F57EF;
        border-radius: 8px;
        padding: 0 16px;
        height: 40px;
        line-height: 40px;
        color: white;
        font-weight: 600;
        letter-spacing: 2px;
        text-align: center;
      }
    }
    .k2_ld_course_description_cont{
      border:0.5px solid #eaeaea;
      border-bottom-left-radius: 10px;
      border-bottom-right-radius:10px;
      padding: 20px 20px;
      h5{
        margin: 0;
        font-size: 20px;
      }
      .course_excerpt{
        margin: 16px 0px;
      }
      .k2_ld_lessons_container{
        display: flex;
        background: #bac0dbf5;
        border-radius: 6px;
        margin: 0 auto;
        padding: 10px;
        gap:15px;
        .k2_ld_lessons_info{
          border-right: 1px solid #0e0e0e;
          width: 50%;
          display: flex;
          align-items: center;
          gap: 15px;
          svg{
            height: 1em;
          }
        }
        .k2_ld_student_info{
          width: 50%;
          display: flex;
          align-items: center;
          gap: 15px;
          svg{
            height: 1em;
          }
        }
        p{
          margin:0;
        }
      }
      .k2_ld_course_button_container{
        display: flex;
        justify-content: flex-start;
        margin-top: 25px;
        a{
          background-color: #2F57EF;
          text-decoration: none !important;
          font-size: 16px;
          color: white;
          padding: 6px 20px;
          border-radius: 6px;
          width: 100%;
          text-align: center;
        }
      }
      .k2_course_progress_bar_container{
        width: 100%;
        height: 8px;
        background: #F0F3F6;
        margin-top: 18px;
        border-radius: 8px;
        .k2_course_grid_progress_line{
          background: #019E7C;
          border-radius: 8px;
          height: 8px;
        }
      }
      .k2_course_progress_text{
          text-transform: uppercase;
          font-weight: 800;
          color: #019e7c;
          margin-left: 5px;
          font-size: 12px;
      }
    }
  }

}


@media only screen and (max-width: 1180px) and (min-width: 767px)  {
  .k2_ld_course_grid_parent_container_save{
    .k2_ld_course_grid_item{
      width: 45%;
      .k2_ld_lessons_container{
        flex-direction: column;
        gap: 0px;
        .k2_ld_lessons_info{
          border-right: none !important;
          width: 100% !important;
          justify-content: center;
          border-bottom: 1px solid #0e0e0e;
          padding-bottom: 15px;
        }
        .k2_ld_student_info{
          width: 100% !important;
          justify-content: center;
        }
      }
    }
  }
}

@media only screen and (max-width: 767px) {
  .k2_ld_course_grid_parent_container_save{
    flex-direction: column;
    .k2_ld_course_grid_item{
      width: 100%;
      .k2_ld_lessons_container{
        flex-direction: column;
        gap: 0px;
        .k2_ld_lessons_info{
          border-right: none !important;
          width: 100% !important;
          justify-content: center;
          border-bottom: 1px solid #0e0e0e;
          padding-bottom: 15px;
        }
        .k2_ld_student_info{
          width: 100% !important;
          justify-content: center;
        }
      }
    }
  }
}